body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: blue;
}

.app-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 20px;
  background-color:lightblue;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

h1 {
  text-align: center;
  color: #f4143d;
}

.form-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.form-inputs,
.form-buttons {
  width: 48%;
}

.form-inputs input,
.form-inputs textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-buttons button {
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
  background-color: #4caf50;
  color: #fff;
  border: none;
  font-size: 16px;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.form-buttons button:hover {
  background-color: #45a049;
}

.student-list {
  list-style: none;
  padding: 0;
}

.student-list li {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: box-shadow 0.3s;
}

.student-list li:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.student-list li div {
  width: 75%;
}
.outer-box button  {
  background-color: #f44336;
  border: none;
  border-radius: 10px;
  color: #fff;
}


.outer-box-button-close:hover {
  background-color: #e6e5e5;
}

.student-list li .actions {
  width: 20%;
  display: flex;
  justify-content: space-between;
}

.student-list button {
  padding: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.student-list button.edit {
  background-color: #2196F3;
  border: none;
  border-radius: 10px;
  color: #fff;
}
.student-list button.view {
  background-color: #21f359;
  border: none;
  border-radius: 10px;
  color: #fff;
}


.student-list button.delete {
  background-color: #f44336;
  border: none;
  border-radius: 10px;
  color: #fff;
}

.student-list button:hover {
  background-color: #ddd;
}